    }
  },
  created () {
    // const obj = {name: '张三'}
    // const map = new Map()
    // map.set(obj, '1')
    // console.log(map)
    // console.log(map.get(obj))
    // console.log('\uD842\uDFB7', '\u20BB7')
    // console.log('\u4e2d', 'U+4e2d')
    // console.log(`你好${obj.name}`)
    // console.log(String.fromCharCode('0x4e2d'))
    this.init()
