<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.home{
  width: 100vw;
  height: 100vh;
  background: rgb(245, 212, 212);
  display: flex;
  flex-direction: column;
}
.mainBox{
  flex: 1;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  padding: 0 30px;
}
.box{
  width: 100px;
  height: 200px;
  background: rgb(182, 157, 241);
  position: relative;
  .text{
    color: red;
  }
  &::before{
    content: "";
    width: 100px;
    height: 100px;
    background: yellowgreen;
    position: absolute;
    top: 0;
    left: 0;
  }
}
.line1{
  width: 100%;
  height: 0;
  margin-top: 100px;
  position: relative;
  /* border-top: 1px solid rgb(46, 231, 160); */
  /* box-sizing: border-box; */
}
.line1::after{
  content:"";
  width: 100%;
  /* height: 1px; */
  border-top: 2px solid rgb(46, 231, 160);
  position: absolute;
  top: 0;
  left: 0;
  transform: scaleY(.5);
}
.line2{
  width: 100%;
  height: 1px;
  margin-top: 100px;
  border-top: 2px solid rgb(46, 231, 160);
}
.line2{

}
.input-btn{
  height: 80px;
  background: #42b983;
}
.ipt-style{
  width: 100%;
  height: 100%;
  line-height: 80px;
}
</style>



